<script lang="ts">
import LazyImage from '@monogrid/vue-boost/core/LazyImage.vue'
import type { PropType } from 'vue'
import { ComponentPublicInstance, computed, defineComponent, shallowRef } from 'vue'

import type { StaticMediaData } from '../../types'

export default defineComponent({
  name: 'StaticImage',
  components: { LazyImage },
  props: {
    data: { type: Object as PropType<StaticMediaData>, required: true },
    /**
     * It defines if the lazy image should be skipped. If a string is passed, it should contain the extension of the files to disable the feature for.
     */
    disableLazy: { type: [Boolean, String] as PropType<boolean | string>, default: 'png' }
  },
  setup (props) {
    const lazyImage = shallowRef<ComponentPublicInstance<typeof LazyImage>>()

    return {
      lazyImage,
      lazySrc: computed(() => {
        if (!props.data?.color || (typeof props.disableLazy === 'boolean' ? props.disableLazy : props.data.ext === props.disableLazy)) return
        return `data:image/svg+xml,%3Csvg%20viewBox%3D%220%200%20${props.data.width}%20${props.data.height}%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Crect%20x%3D%220%22%20y%3D%220%22%20width%3D%22${props.data.width}%22%20height%3D%22${props.data.height}%22%20fill%3D%22${encodeURIComponent(props.data.color)}%22%20%2F%3E%3C%2Fsvg%3E`
      })
    }
  }
})
</script>

<template>
  <LazyImage
    ref="lazyImage"
    :lazy-src="lazySrc"
    :width="data.width"
    :height="data.height"
    :src="data.src"
  />
</template>
